import React from "react";
import "./image.css";

interface ImageProps {

  /**
   * React className 설정
   */
  className?: string[];

  /**
   * 버튼 내부 내용 설정
   */
  label?: string;
  /**
   * 클릭 핸들러
   */
  onClick?: () => void;

  /**
   * style 설정 
   */
  style?: object;

  /**
   * src 설정 
   */
  src: string;

  /**
   * alt 설정 
   */
  alt: string;
}

/**
 * Primary UI component for user interaction
 */
export const Button = ({
  label,
  className,
  style,
  onClick,
  src,
  alt
}: ImageProps) => (
  <img
    style={style}
    className={`${className.join(" ")}`}
    onClick={onClick}
    src={src}
    alt={alt}
  >
  </img>
);

/**
 * 각 아이콘 버튼은 미리 컴포넌트로 각각 만들어서 사용할 예정
 * 
 */
export const IconButton = () => (
  <MdModeEditOutline/>
)

export const FindIconButton = () => (
  <MdFindInPage/>
)



/**
 * react/require-default-props
 */
Button.defaultProps = {
  className: [],
  style: { },
  onClick: () => { },
  label: 'this is button'
};

export default Button;
